import react from "react";

import logo from "../images/company/logo_company.jpg"
const OurCompany = () => {
  return (
    <react.Fragment>
      <h1>walcome Our Company</h1>

      <div className="card mb-3 about-company" style={{maxWidth: "540px"}}>
                <div className="row g-0">
                    <div className="col-md-4">
                        <img src={logo} className="img-fluid rounded-start" alt="...loading" />
                    </div>
                    <div className="col-md-8">
                    <div className="card-body">
                        <h5 className="card-title">Soaq Al-Arab</h5>
                        <p className="card-text"> This is a commercial site that provides many services, including online marketing, whether through a process such as (purchasing a product - offering a product for sale).</p>
                        <p className="card-text">
                          <small class="text-muted">Founded 23 years ago</small>
                        </p>
                    </div>
                    </div>
                </div>
            </div>

    </react.Fragment>
  );
};

export default OurCompany;
