import React, { Component } from "react";

import { Link } from "react-router-dom";

class Product extends Component {
  getclasses() {
    return this.props.dataprpduct.amount === 0
      ? "badge bg-warning m-2"
      : "badge bg-success m-2";
  }

  render() {
    return (
      <section className="section-cart">
        <div className="container">
          <div className="row">
            <div className="col-3">
              <Link to={`/product/${this.props.dataprpduct.id}`}>
                {this.props.dataprpduct.name}
              </Link>
            </div>
            <div className="col">
              <span className={this.getclasses()}>
                {this.props.dataprpduct.amount}
              </span>
              <button
                className="btn btn-primary btn-sm m-2"
                style={{padding: "2px 20px", fontWeight: "bold", fontSize: "20px"}}
                onClick={() => this.props.onadd(this.props.dataprpduct)}
              >
                +
              </button>
              <button
                className="btn btn-primary btn-sm m-2"
                style={{padding: "2px 20px", fontWeight: "bold", fontSize: "20px"}}
                onClick={() => this.props.onsub(this.props.dataprpduct)}
              >
                -
              </button>
              <span style={{ cursor: "pointer", margin: "50px", color: "brown" }}>
                <i
                  className="fas fa-trash-restore-alt"
                  onClick={() => this.props.ondelete(this.props.dataprpduct)}
                ></i>
              </span>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Product;
