import { NavLink } from "react-router-dom";

const Navbar = (props) => {
  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      <div className="container-fluid">
        <NavLink className="navbar-brand" to="#">
          Arab-Suaq
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
          <div className="navbar-nav">
            <NavLink className="nav-link" aria-current="page" to="/home">
              Home
            </NavLink>
            <NavLink className="nav-link" to="/about">
              About
            </NavLink>
            <NavLink className="nav-link" to="/cart">
              ShoopingCart
            </NavLink>
            {/* <NavLink className="nav-link" to="/product/:id">ProductDetalis</NavLink> */}
            <NavLink className="nav-link" to="/admin">
              AdminCart
            </NavLink>
            <NavLink className="nav-link" to="/connact">
              Connact
            </NavLink>
          </div>
        </div>
        <span className="badge bg-success m-2">{props.numproducts}</span>
        <NavLink  style={{ padding: " 8px 20px", border: "black", backgroundColor: 'black' }} className=" btn btn-primary" to="/login"> Log-In</NavLink>
      </div>
    </nav>
  );
};

export default Navbar;
