import React, { Component } from "react";

import Product from "./product";

class ShopingCard extends Component {
  render() {
    const { onrest, data, ondelete, onincrement, ondecrement } = this.props;
    return (
      <React.Fragment>
        <section className="product-cart">
          <main className="container">
            <h2 className="txt_head">Shoping Cart</h2>
            <button style={{ padding:" 10px 40px"}} className="btn btn-danger m-4" onClick={onrest}>
              Rest
            </button>

            <div className="accordion" id="accordionPanelsStayOpenExample">
              <div className="accordion-item">
                <h2 className="accordion-header" id="panelsStayOpen-headingOne">
                  <button
                    className="accordion-button"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#panelsStayOpen-collapseOne"
                    aria-expanded="true"
                    aria-controls="panelsStayOpen-collapseOne"
                  >
                    # illustration
                  </button>
                </h2>
                <div
                  id="panelsStayOpen-collapseOne"
                  className="accordion-collapse collapse show"
                  aria-labelledby="panelsStayOpen-headingOne"
                >
                  <div className="accordion-body">
                    <strong>Explanation of adding the product to the Arab market.</strong>
                    <ol>
                        <li>Click on the name of the product you want to know all the details about it.</li>
                        <li>
                            Press + to increase the quantity you want from each product:
                            <ul>
                                <li>you will see each increase made.</li>
                            </ul>
                        </li>
                        <li>
                        Press + to reduce the amount of product you want:
                            <ul>
                                <li>you will see all the decreasing.</li>
                            </ul>
                        </li>
                        <li>Click the delete button to be able to delete any product you want.</li>
                    </ol>
                  </div>
                </div>
              </div>
            </div>

            {data.map((x) => (
              <Product
                key={x.id}
                dataprpduct={x}
                ondelete={ondelete}
                onadd={onincrement}
                onsub={ondecrement}
              />
            ))}
          </main>
        </section>
      </React.Fragment>
    );
  }
}

export default ShopingCard;
