import React, { Component } from "react";

import axios from "axios";

class EditProduct extends Component {
  state = {
    id: "",
    name: "",
    amount: "",
  };

  async componentDidMount() {
    const id = this.props.match.params.id;
    if (id !== "new") {
      const { data } = await axios.get(`http://localhost:3000/products/${id}`);

      //clone
      const state = { ...this.state };
      //edit
      state.name = data.name;
      state.amount = data.amount;
      state.id = data.id;
      //setstate
      this.setState(state);
    }
  }
  handersubmit = async (e) => {
    e.preventDefault();

    if (this.props.match.params.id === "new") {
      await axios.post("http://localhost:3000/products", this.state);
    } else {
      const obj = { ...this.state };
      delete obj.id;

      await axios.put(`http://localhost:3000/products/${this.state.id}`, obj);
    }

    this.props.history.replace("/admin");
    window.location.reload();
    };

  handelchange = (e) => {
    //clone
    const state = { ...this.state };
    //edit
    state[e.currentTarget.name] = e.currentTarget.value;
    //setstate
    this.setState(state);
  };

  render() {
    return (
      <React.Fragment>
        <section className="section-edit">
          <div className="container">
            <h2 className="txt_head">
              {this.props.match.params.id === "news"
                ? "Add Product"
                : "Edit Product"}
            </h2>
            <div className="container">
              <form onSubmit={this.handersubmit}>
                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Product Name</label>
                  <input
                    name="name"
                    value={this.state.name}
                    onChange={this.handelchange}
                    type="text"
                    className="form-control"
                    id="exampleInputEmail1"
                    aria-describedby="emailHelp"
                    placeholder="The Name"
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="exampleInputPassword1">Product Amount</label>
                  <input
                    name="amount"
                    value={this.state.amount}
                    onChange={this.handelchange}
                    type="number"
                    className="form-control"
                    id="exampleInputPassword1"
                    placeholder="The Amount"
                  />
                </div>
                <div className="form-check">
                </div>
                <div className="text-center">
                  <button
                    style={{ padding: " 10px 40px" }}
                    type="submit"
                    className="btn btn-primary"
                  >
                    {this.props.match.params.id === "new" ? "Add" : "Edit"}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </section>
      </React.Fragment>
    );
  }
}

export default EditProduct;
