import React, { Component } from "react";

class Admin extends Component {
    render() {
        const { products, ondelete } = this.props;
        return (
            <React.Fragment>
                <section className="section-opt">
                    <div className="container-fluid">
                        <h2 className="txt_head">The Admin</h2>

                        <button
                            style={{ padding: " 10px 40px" }}
                            className="btn btn-primary m-4"
                            onClick={() => {
                                this.props.history.push("/Edit_product/new");
                            }}
                        >
                            Add
                        </button>
                        <table className="table">
                            <thead>
                                <tr>
                                    <th scope="col">Name</th>
                                    <th scope="col">Prise</th>
                                    <th scope="col">Edit</th>
                                    <th scope="col">Delete</th>
                                </tr>
                            </thead>
                            <tbody>
                                {products.map((item) => {
                                    return (
                                        <tr key={item.id}>
                                            <th scope="row">{item.name}</th>
                                            <td>{item.amount}</td>
                                            <td>
                                                <i
                                                    style={{ cursor: "pointer", color: "darkblue" }}
                                                    onClick={() => {
                                                        this.props.history.push(`/Edit_product/${item.id}`);
                                                    }}
                                                    className="fas fa-edit"
                                                ></i>
                                            </td>
                                            <td>
                                                <i
                                                    style={{ cursor: "pointer", color: "brown" }}
                                                    onClick={() => ondelete(item)}
                                                    className="fas fa-trash"
                                                ></i>
                                            </td>
                                        </tr>
                                    );
                                })}
                            </tbody>
                        </table>
                    </div>
                </section>
            </React.Fragment>
        );
    }
}

export default Admin;
