import React, {Component} from "react"

import Joi from "joi-browser"

class Log_in extends Component{
    state = {
        username: "",
        password: "",
        errors: {}
    }

    schema = {
        username: Joi.string().required(),
        password: Joi.string().required()
    }


    handelsubmit = (e)=>{
        e.preventDefault()

        const valid = this.valedation()

        if(valid) return;
        
        console.log("submit succses")
    }

    handelchange = (e)=>{
        //clone
        const state = {...this.state};
        //edit
        state[e.currentTarget.name] = e.currentTarget.value
        //setstate
        this.setState(state)
    }
/*
    valedation = ()=>{
        //clone
        const errors = {}
        //edit
        if(this.state.username.trim() ===  "")
            errors.username = "please enter username"
        if(this.state.password.trim() ===  "")
        errors.username = "please enter password"
        //setstate
        this.setState({errors})

        return Object.keys(errors).length === 0? null: errors
    }
    */
    valedation = ()=>{
        //clone
        const errors= {}

        const state = {...this.state}
        delete state.errors

        const result = Joi.validate(state, this.schema, {abortEarly: false})
        console.log(result)

        //edit
        if(result.error != null){
            for(const res of result.error.details){
                console.log(res)
                errors[res.path] = res.message
            }
        }
        //setstate
        this.setState({errors})

    }
    render(){
        console.log(this.state)

        return(
            <React.Fragment>
                <section className="login">
                    <div className="container">
                        <h2 className="txt_head" >Log-In</h2>
                        <form onSubmit={this.handelsubmit}>
                            <div className="form-group">
                                <label htmlFor="exampleInputEmail1">Email address</label>
                                <input
                                    name="username"
                                    value={this.state.username}
                                    onChange={this.handelchange}
                                    type="email" 
                                    className="form-control" 
                                    id="exampleInputEmail1" 
                                    aria-describedby="emailHelp" 
                                />
                                {this.state.errors.username &&
                                <div class="alert alert-danger" role="alert">
                                    {this.state.errors.username}
                                </div>}
                            </div>
                            <div className="form-group">
                                <label htmlFor="exampleInputPassword1">Password</label>
                                <input
                                    name="password"
                                    value={this.state.password}
                                    onChange={this.handelchange} 
                                    type="password" 
                                    className="form-control" 
                                    id="exampleInputPassword1" 
                                />
                                {this.state.errors.password && 
                                <div class="alert alert-danger" role="alert">
                                    {this.state.errors.password}
                                </div>}
                            </div>
                            <div className="form-group form-check">
                                <input type="checkbox" className="form-check-input" id="exampleCheck1" />
                                <label className="form-check-label" htmlFor="exampleCheck1">Check me out</label>
                            </div>
                            <div className="text-center">
                                <button style={{ padding: " 10px 40px" }} type="submit" className="btn btn-primary">Log-In</button>
                            </div>
                        </form>
                    </div>
                </section>
            </React.Fragment>
        )
    }
}


export default Log_in