import react from "react"

import Typography from "@mui/material/Typography";

import { DataGrid } from "@mui/x-data-grid";

import header_img from "../images/header/header.png"

const columns = [
  { field: "id", headerName: "ID", width: 70 },
  { field: "firstName", headerName: "First name", width: 130 },
  { field: "ggggggg", headerName: "Last GGG", width: 130 },
  {
    field: "age",
    headerName: "Age",
    type: "number",
    width: 90,
  },
  {
    field: "fullName",
    headerName: "Full name",
    description: "This column has a value getter and is not sortable.",
    sortable: false,
    width: 160,
    valueGetter: (params) =>
      `${params.getValue(params.id, "firstName") || ""} ${
        params.getValue(params.id, "ggggggg") || ""
      }`,
  },
];

const rows = [
  { id: 1, ggggggg: "Snow", firstName: "Jon", age: 35 },
  { id: 2, ggggggg: "Lannister", firstName: "Cersei", age: 42 },
  { id: 3, ggggggg: "Lannister", firstName: "Jaime", age: 45 },
  { id: 4, ggggggg: "Stark", firstName: "Arya", age: 16 },
  { id: 5, ggggggg: "Targaryen", firstName: "Daenerys", age: null },
  { id: 6, ggggggg: "Melisandre", firstName: null, age: 150 },
  { id: 7, ggggggg: "Clifford", firstName: "Ferrara", age: 44 },
  { id: 8, ggggggg: "Frances", firstName: "Rossini", age: 36 },
  { id: 9, ggggggg: "Roxie", firstName: "Harvey", age: 65 },
];

const Home = (props) => {
  return (
    <react.Fragment>
      <header>
        <div className="container-fluid">
          <div className="row">
            <div className="col-6" style={{position: "relative"}}>
              <div className="header_info">
                <h1>Get Shopping</h1>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Iste, nostrum hic. Quisquam delectus culpa exercitationem, ad quidem ullam nam, atque numquam laudantium, eos molestiae? Asperiores nesciunt doloribus expedita assumenda cumque!</p>
                <button className="btn btn-primary">Shoping Now</button>
              </div>
            </div>
            <div className="col-6" style={{padding: '0px'}}>
              <img className="img_header" src={header_img} alt="..loading" />
            </div>
          </div>
        </div>
      </header>

    <section className="home">
      <div className="container">
        <h1 className="text-center m-4" style={{ fontFamily: "cursive" }}>
          Souk Al-Arab
        </h1>
        <Typography variant="h4"> Customer Data: </Typography>

        <div className="Table_home">
          <DataGrid
            rows={rows}
            columns={columns}
            pageSize={5}
            rowsPerPageOptions={[5]}
            checkboxSelection
          />
        </div>
      </div>
    </section>

    <section className="products m-4">
      <div className="container">
        <h2 className="txt_head" style={{margin: "20px 0"}}>Advertising</h2>
        <div className="row row-cols-1 row-cols-md-3 g-4">
          <div className="col">
            <div className="card">
              <img src="https://www.marketing91.com/wp-content/uploads/2018/09/How-to-Sell-a-Product-with-Blogging-4.jpg" className="card-img-top" alt="..." />
              <div className="card-body">
                <h5 className="card-title">Card title</h5>
                <p className="card-text">This is a longer card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
              </div>
            </div>
          </div>
          <div className="col">
            <div className="card">
              <img src="https://www.finplus.co.in/wp-content/uploads/2017/05/Sell-products-online-why-should-I-start-selling-online.jpg" className="card-img-top" alt="..." />
              <div className="card-body">
                <h5 className="card-title">Card title</h5>
                <p className="card-text">This is a longer card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
              </div>
            </div>
          </div>
          <div className="col">
            <div className="card">
              <img src="https://www.edesk.com/wp-content/uploads/2021/03/find-trending-products-sell-ecommerce.png" className="card-img-top" alt="..." />
              <div className="card-body">
                <h5 className="card-title">Card title</h5>
                <p className="card-text">This is a longer card with supporting text below as a natural lead-in to additional content.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    </react.Fragment>
  );
};

export default Home;
