import React, {Component} from "react";

import qs from "query-string";

class ProductDetelis extends Component{

    handelsve = ()=>{
        this.props.history.replace("/cart")
        console.log(this.props.history)
    }

    render(){
        console.log(this.props.match.params.id)
        const product = this.props.products.filter(c => c.id === parseInt(this.props.match.params.id))[0]
        //console.log(product)
    
        const res = qs.parse(this.props.location.search)
        console.log(res);
    
        return(
            <React.Fragment>
                <section className="product_detalis">
                    <div className="container">
                        <h2 className="txt_head m-4">Product Detalis For number({this.props.match.params.id})</h2>
                        <h3>- Product Name : <span className="spn_highliet">{product.name}</span></h3>
                        <h3>- Product Amount in shooping Cart : <span className="spn_highliet">{product.amount}</span></h3>
                        <div className="text-center m-4">
                            <button style={{ padding:" 10px 40px"}} className="btn btn-primary" onClick={this.handelsve}>save</button>
                        </div>
                    </div>
                </section>
            </React.Fragment>
        )
    }
}


export default ProductDetelis;