import react from "react";

import { Route, Link } from "react-router-dom";

import OurTeam from "./ourteam";
import OurCompany from "./ourcompany";

const About = (props) => {
  return (
    <react.Fragment>
      <section className="about">
        <div className="container">
        <h1 className="txt_head" style={{marginTop: "20px "}}>About</h1>
          <h3 className="text-center head_about m-4">
            Click on any of the following buttons to get an introduction<br/>
            (introduction to the designer team - company profile).
          </h3>

          <div className="row">
            <div className="col-3">
              <ul className="m-4 list-unstyled">
                <li>
                  <Link className="btn btn-primary m-2" to="/about/team">
                    our team
                  </Link>
                </li>
                <li>
                  <Link className="btn btn-primary m-2" to="/about/company">
                    our company
                  </Link>
                </li>
              </ul>
            </div>
            <div className="col">
              <Route path="/about/team" component={OurTeam} />
              <Route path="/about/company" component={OurCompany} />
            </div>
          </div>
        </div>
      </section>
    </react.Fragment>
  );
};

export default About;
