import React, {Component} from "react";

import Navbar from "./navbar";

import { Route, Switch, Redirect } from 'react-router-dom';

import Home from "./home";

import About from "./about";
import Connact from "./connact";

import ShopingCard from "./shopingCard";
import ProductDetelis from "./porductdetelis"

import NotFoundPage from "./notFound"

import Log_in from "./login"
import Admin from "./admin"
import EditProduct from "./edit_product"

import Footer from "./footer"

import axios from "axios";

import { ToastContainer} from "react-toastify"

class App extends Component{

    state = {
        products:[]
    }

    incremantkhandelar = (h)=>{
        const products = [...this.state.products]
        const index = products.indexOf(h)
        products[index] = {...products[index]}
        products[index].amount++;

        this.setState({products});
    };

    decremantkhandelar = (h)=>{
        const products = [...this.state.products]
        const index = products.indexOf(h)
        products[index] = {...products[index]}
        if( products[index].amount !== 0){
            products[index].amount--;
        }
        this.setState({products});
    };

    deletehandeler = async (p)=>{
        //old copy
        const OldProduct = [...this.state.products]

        // clone and edit
        const newproduct = this.state.products.filter((v)=>{
            return v.id !== p.id
        });
        //set state
        this.setState({products: newproduct});

        try{
            //call backend
            await axios.delete(`http://localhost:3000/products/${p.id}`)
        }catch (ex){
            alert("Can't Delete")

            //set state
            this.setState({products: OldProduct});
        }

        
    }

    resthandeler = ()=>{
        let copyproduct = [...this.state.products]
         copyproduct = this.state.products.map((m)=>{
            m.amount = 0
            return m;
        })

        this.setState({products: copyproduct})
    }

    async componentDidMount(){
        const res = await axios.get("http://localhost:3000/products")
        console.log(res.data)

        this.setState({products: res.data})
        /*fetch('https://jsonplaceholder.typicode.com/posts')
        .then(response => response.json())
        .then(json => console.log(json))*/
    }

    render(){
        return(
            <React.Fragment>
                <ToastContainer />
                <Navbar numproducts={this.state.products.length} />
                
                <Switch>
                    <Route path="/login" component={Log_in} />

                    <Route path="/product/:id" render={(props)=>(
                        <ProductDetelis
                            products = {this.state.products}
                            {...props}
                        />
                    )} />

                    <Route path="/admin" render={(props)=>(
                        <Admin
                            products = {this.state.products}
                            ondelete = {this.deletehandeler}
                            {...props}
                        />
                    )} />

                    <Route path={"/Edit_product/:id"} render={(props)=>(
                        <EditProduct
                            {...props}
                        />
                    )}/>

                    <Route path="/cart" render={(props) => (
                        <ShopingCard 
                            data = {this.state.products}
                            onincrement = {this.incremantkhandelar}
                            ondecrement = {this.decremantkhandelar}
                            ondelete = {this.deletehandeler}
                            onrest = {this.resthandeler}
                            {...props}
                        />
                    )} />

                    <Route path="/about" component={About} />
                    <Route path="/notfound" component={NotFoundPage} />
                    <Route path="/connact" component={Connact} />
                    <Route path="/home" component={Home} />
                    <Redirect from="/" to="/home" />
                    <Redirect to="/notfound" />

                </Switch>

                <Footer />

                {/*<ShopingCard 
                    data = {this.state.products}
                    onincrement = {this.incremantkhandelar}
                    ondelete = {this.deletehandeler}
                    onrest = {this.resthandeler}
                />*/}

            </React.Fragment>
        )
    }
}

export default App;